import { Injectable, SecurityContext } from '@angular/core';
import { get, map } from 'lodash';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';

@Injectable({
  providedIn: 'root'
})
export class RxUrlUtilsService {
  constructor(private router: Router, private domSanitizer: DomSanitizer) {}

  buildUrl(baseUrl: string, ...queryParams: Object[]): string {
    let url = baseUrl;
    const queryString = this.toQueryString(...queryParams);

    if (queryString) {
      url += '?' + queryString;
    }

    return url;
  }

  isValidUrl(url: string, isProtocolRequired = true): boolean {
    const regExp = isProtocolRequired
      ? /^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/
      : /^((https?|ftp):\/\/)?[^\s/$.?#].[^\s]*$/;

    return regExp.test(url);
  }

  toQueryString(...queryParams: Object[]): string {
    return map(queryParams, (params) =>
      map(params as any, (value, key) => `${encodeURIComponent(key)}=${encodeURIComponent(value)}`).join('&')
    ).join('&');
  }

  getBundleIdFromUrl(url?: string): string {
    return get(
      this.router.parseUrl(url ?? window.location.hash.substring(1)),
      'root.children.primary.segments[0].path'
    );
  }

  isUrlSafe(url: string): boolean {
    const sanitizedUrl = this.domSanitizer.sanitize(SecurityContext.URL, url);

    return url === sanitizedUrl;
  }
}
