import { Directive, Input } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, ValidationErrors, Validator } from '@angular/forms';
import { IUniqueValidatorConfig } from './unique-validator.types';
import { includes } from 'lodash';

@Directive({
  selector: '[rxUnique]',
  providers: [{ provide: NG_VALIDATORS, useExisting: RxUniqueValidator, multi: true }]
})
export class RxUniqueValidator implements Validator {
  @Input()
  rxUnique!: IUniqueValidatorConfig;

  validate(control: AbstractControl): ValidationErrors | null {
    let result = null;

    if (control.dirty && control.value && includes(this.rxUnique.items, control.value)) {
      result = {
        error: {
          message: this.rxUnique.errorMessage
        }
      };
    }

    return result;
  }
}
