import { HttpResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { saveAs } from 'file-saver';
import { replace, words } from 'lodash';

@Injectable({
  providedIn: 'root'
})
export class RxFileService {
  static saveFile(fileStream: HttpResponse<ArrayBuffer>) {
    const arrayBufferView = new Uint8Array(fileStream.body as ArrayBuffer);
    const fileName = RxFileService.extractFileName(fileStream);

    const file = new Blob([arrayBufferView], {
      type: fileStream.headers.get('content-type') ?? undefined
    });

    saveAs(file, fileName);
  }

  static extractFileName(fileStream: HttpResponse<ArrayBuffer>): string | undefined {
    const contentDisposition = fileStream.headers.get('content-disposition') ?? '';

    /*
     *  words('attachment; filename = logs.tar.gz'.replace(/\s/g, ''), /filename=(.*)/)[1];
     *  => 'logs.tar.gz'
     */
    return words(contentDisposition.replace(/\s/g, ''), /filename=(.*)/)[1];
  }

  createAndDownloadBlob(data: any, type: string, name: string, extension: string): void {
    const blob: Blob = new Blob([data], { type });

    saveAs(blob, `${name}.${extension}`);
  }

  extractFileName(fileStream: HttpResponse<ArrayBuffer>): string | undefined {
    return RxFileService.extractFileName(fileStream);
  }
}
