import { Directive, Input, OnInit } from '@angular/core';
import { AbstractControl, NG_VALIDATORS, ValidationErrors, Validator } from '@angular/forms';
import { IJsonValidatorConfig } from './json.types';
import { TranslateService } from '@ngx-translate/core';
import { get } from 'lodash';

@Directive({
  selector: '[rxJson]',
  providers: [{ provide: NG_VALIDATORS, useExisting: RxJsonValidator, multi: true }]
})
export class RxJsonValidator implements OnInit, Validator {
  @Input()
  rxJson!: IJsonValidatorConfig;

  private errorMessage = '';

  constructor(private translateService: TranslateService) {}

  ngOnInit() {
    this.errorMessage = get(
      this.rxJson,
      'errorMessage',
      this.translateService.instant('com.bmc.arsys.rx.client.utils.json-validator.error-message')
    );
  }

  validate(control: AbstractControl): ValidationErrors | null {
    let result = null;

    if (control.value) {
      try {
        JSON.parse(control.value);
      } catch (ignored) {
        result = {
          error: {
            message: this.errorMessage
          }
        };
      }
    }

    return result;
  }
}
