import { forEach, isEmpty, size } from 'lodash';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class RxTreeService {
  flatten(tree: any, childrenPropertyName: string = 'children'): any[] {
    const flattenElements: any[] = [];
    const stack = [];
    let currentNode: any;

    stack.push(tree);

    while (stack.length > 0) {
      currentNode = stack.pop();

      flattenElements.push(currentNode);

      if (!isEmpty(currentNode[childrenPropertyName])) {
        currentNode[childrenPropertyName].forEach((childrenNode: any) => {
          stack.push(childrenNode);
        });
      }
    }

    return flattenElements;
  }

  flattenTree(tree: any, ...childrenPropertyNames: string[]): any[] {
    let queue: any[] = [];
    let currentIndex = 0;
    let currentNode: any;

    queue.push(tree);

    while (currentIndex < queue.length) {
      currentNode = queue[currentIndex++];

      if (currentNode) {
        forEach(childrenPropertyNames, function (propertyName) {
          if (size(currentNode[propertyName]) > 0) {
            queue = queue.concat(currentNode[propertyName]);
          }
        });
      }
    }

    return queue;
  }

  flattenBy(tree: any, predicate: (node: any) => any[]): any[] {
    const flattenElements = [];
    const stack = [];
    let currentNode = null;

    stack.push(tree);

    while (stack.length > 0) {
      currentNode = stack.pop();

      flattenElements.push(currentNode);

      predicate(currentNode).forEach((childrenNode) => {
        stack.push(childrenNode);
      });
    }

    return flattenElements;
  }
}
